import React, { useState } from 'react';
import {
    Route,
    NavLink,
    HashRouter
  } from "react-router-dom";
import Home from "./Home";
import Content from "./contact";
import { motion } from 'framer-motion';
import logo from "./assets/logo.png";
import ReactRotatingText from 'react-rotating-text';
import Button from '@material-ui/core/Button';
import { createMuiTheme, MuiThemeProvider, createStyles } from '@material-ui/core/styles';
import  MyChatBot  from "./MyChatBot";


function Main (props){

const [aboutMe, setAboutMe] = useState(true);
const [contact, setContact] = useState(false);

const handleAboutClick = props => {
    setAboutMe(true);
    setContact(false);
};

const handleContactClick = props => {
    setAboutMe(false);
    setContact(true);
};
    return( 
        
    <div style={{flexDirection:"column"}}>
        <HashRouter>
            <div>
            <MuiThemeProvider theme={darkTheme}>
                <div className="header-div">
                    <div className="header-static">
                        <motion.div
                            animate={{rotateX:360}}
                            transition={{duration:0.75}}
                            whileHover={{ scale:1.2}}>
                               <a href="https://medium.com/thrive-global/ikigai-the-japanese-secret-to-a-long-and-happy-life-might-just-help-you-live-a-more-fulfilling-9871d01992b7" target="_blank"> <img className="logo" src= { logo } /></a>
                        </motion.div>
                        <p><ReactRotatingText items={['EAT.CODE.REPEAT']}  typingInterval={200} /></p>
                    </div>
                    
                        <ul className="header-nav">
                           <motion.div whileHover={{scale:1.1}}><li><NavLink exact to="/" > <Button onClick={ handleAboutClick } variant={ aboutMe? "contained":"outlined"} color="primary" textColor="#fff">About Me</Button> </NavLink></li></motion.div>
                            <motion.div whileHover={{scale:1.1}}><li><NavLink to="/contact"><Button onClick={ handleContactClick } variant={contact? "contained":"outlined"} color="primary">Contact</Button></NavLink></li></motion.div>
                            
                        </ul>
                    
                    
                   
                </div>  
                
             
                <div className="content">      
                <motion.div
                initial={{scale:0.75}}
                animate={{ scale: 1 }}
                transition={{ duration: 0.75 }}>   
                    <Route exact path="/" component={Home}/>
                    
                    <Route path="/contact" component={Content}/>
                </motion.div>
                </div>
                </MuiThemeProvider>
            </div>
        </HashRouter>
        <div className="ch-bot">
            <MyChatBot />
        </div>
    </div>
    );
};

export default Main;



const darkTheme = createMuiTheme({
    overrides:{
        MuiTextField: createStyles({
            root: {
                "&& label.Mui-focused": {
                  color: "aqua"
                },
                "& .MuiInput-underline:after": {
                  borderBottomColor: "aqua"
                },
                "& .MuiOutlinedInput-root": {
                  "& fieldset": {
                    borderColor: "grey"
                  },
                  "&:hover fieldset": {
                    borderColor: "white"
                  },
                  "&.Mui-focused fieldset": {
                    borderColor: "aqua",
                  },
                
                }
              }
        })
    },
    palette: {
        type: 'dark',
        action:{
            selected:"#18ffff",
            active: "#18ffff",
            hover:"red",
            selected: '#18ffff'
        },
      primary: {
        light: "#18ffff",
        main: "#18ffff",
        dark: "#18ffff"
    
      },
      secondary: {
          light: "#84ffff",
          main: "#84ffff",
          dark: "#84ffff"
      },
      

    
}
  });


  